import ENSName from 'components/ens/ENSName.js';
import ShareButton from 'components/nft-jacker/ShareButton.js';
import Tone from 'tone';
import PlayButton from 'components/buttons/PlayButton.js';
import RemixDetails from './RemixDetails.js';
import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import LoadingRemix from './LoadingRemix.js';
import Zorb from './Zorb.js';
import ScrubBar from './ScrubBar.js';
import './currently-playing.scss';

const CurrentlyPlaying = ({songId, remixId, duration, startTime, audioPlayer, loading, togglePlay, isPlaying, seek, playing, remixStats}) => { 
    let [fullscreen, setFullScreen] = useState(false);
    let [showShareModal, setShowModal] = useState(false);

    useEffect(() => {
        if (playing && !loading) {
            setFullScreen(true);
        }
    }, [playing, loading]);
    
    if (!playing) {
        return '';
    }
    let zorbId = playing && playing.metadata.attributes.find(x => x.trait_type === 'zorb').value;
    let genImg = playing.metadata.image;
    return (
        [
            fullscreen &&
                <div className="fullscreen-backdrop"
                     onClick={() => setFullScreen(false)}
                >
                  <ShareButton
                    setShowModal={setShowModal}
                    type="remix"
                    showShareModal={showShareModal}
                    url={"spores.vision/zp/" + songId + "/" + remixId}
                    lowerGradInverted={"black"}
                    buttonBgColor={"white"}/>
                  {!showShareModal && <RemixDetails duration={duration} isPlaying={isPlaying} stats={remixStats[playing.tokenId]} remix={playing}/>}
                </div>,
        <div className="currently-playing">
          {loading && <LoadingRemix/>}
          {!loading && <PlayButton resume={true} play={(x) => {
              togglePlay(x);
          }} isPlaying={isPlaying}/>}
          {!loading && <ScrubBar audioPlayer={audioPlayer} startTime={startTime} isPlaying={isPlaying}/>}
          {!loading && <img onClick={() => setFullScreen(true)} src={genImg} className="gen-img pushable"/>}
          {!loading && playing && !loading && <Zorb className="zorb" zorbId={zorbId}/>}
          {!loading && <ENSName address={playing.owner}/>}

        </div>
            ]
        );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(CurrentlyPlaying);
