import React, { useEffect, useRef, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {useInterval} from 'components/hooks/useInterval.js';
import './animation.scss';

const width = 100;
const height = 100;

const convert = ({x,y}) => {
    return {
        x: 50 + x*width,
        y: height - (50 + y*height)
    };
}

const RemixAnimation = ({animationData, isPlaying, duration}) => { 
    let startTime = useRef();
    let [coord, setCoord] = useState({x: 50, y:50});

    useEffect(() => {
        if (isPlaying) {
            startTime.current = new Date().getTime();
        }
    }, [isPlaying]);

    const onTick = useCallback(() => {
        if (!isPlaying) {
            return;
        }
        let timeElapsed = (new Date().getTime() - startTime.current);;
        timeElapsed /= 1000;
        timeElapsed = timeElapsed % duration;
        let last = undefined;
        let i=0;
        for (let {time, movement} of animationData) {
            if (timeElapsed <= time) {
                setCoord(convert(movement));
                break;
            }
            last = time;
            i++;
        }
    }, [setCoord, isPlaying, animationData, duration]);

    useInterval(onTick, 40);
    
    return (<div className="player-animation">
              <svg className="absolute top-0 left-0 z-30" width={width} height={height}>
                <circle cx={coord.x} cy={coord.y} r={10} fill="red"/>
              </svg>
              <img className="absolute z-1 top-0 left-0" src="/player-outline.svg"/>
            </div>
           );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixAnimation);
