import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {useInterval} from 'components/hooks/useInterval.js';

const TextAndTime = ({timeElapsed}) => { 
    let ref = useRef(0);
    let [text, setText] = useState("wants to play");

    const onTick = useCallback(() => {
        setText(undefined);
        ref.current++;
        if (ref.current % 2 === 1) {
            setText("wants to play");
        }else {
            setText("leaked " + leakTime(timeElapsed) + " ago");
        }
    }, [text, timeElapsed]);

    useInterval(onTick, 4000);
    
    if (!text) {
        return '';
    }
    return (<span className={"time-and-text italic ml-1 " + (ref.current % 2 === 0 ? " odd " : " even")}>{text}</span>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

const leakTime  = (time) => {
    let minutes = time / 1000 / 60;
    if (minutes < 60) {
        return Math.ceil(minutes) + " min. ";
    }

    let hours = minutes / 60;
    if (hours < 48) {
        return Math.ceil(hours) + " hours "
    }

    let days = hours / 24;
    return Math.round(days) + " days ";
};



export default connect(mapStateToProps, mapDispatchToProps)(TextAndTime);
