import React, { useEffect, useRef, useCallback, useState} from 'react';
import {getSongs} from 'stems/getStemz.js';
import {connect} from 'react-redux';
import {setSongLoading, setSongLoaded, setLeakLoaded} from 'actions/index.js';
import SliceWaveform from './SliceWaveform.js';
import {useRecordingListener} from './useRecordingListener.js';
import './recorder.scss';

/**
 * When the user clicks the "red" "finished recording" button, this view
 * is shown. It is the top-level component of the entire record -> mint flow

 1. Receives all the recording data from the iframe [this component handles it]
 2. Shows waveform allowing cropping the recording to taste (SliceWaveform)
 3. Shows a "caption" input field for writing text to go w/ the recording
 4. Crops the screenshot of the generative art (and uploads to IPFS)
 5. Triggers a transaction for minting the "sliced recording" + caption + art as an NFT
 */

const SliceRecorder = ({playerRef, zorbId, setIsSlicing, songId, setSongLoaded, setSongLoading, setLeakLoaded}) => { 
    let {
        buffer,
        screenshot,
        silence,
        beatGuide,
        startTime,
        startStep,
        endTime,
        stemsData,
        loopsData,
        onCancel,
        remixesHistory,
        zorbMovements,
        waveform} = useRecordingListener();

    const [waitingForSlice, setWaitingForSlice] = useState(false);

    useEffect(() => {
        if (buffer) {
            setIsSlicing(true);
        }
    }, [buffer, setIsSlicing]);

    const loadSong = useCallback(async (switchSongs) => {
        let songs = await getSongs();
        let song;
        if (switchSongs === true) {
            songs.sort((a,b) => a.songId - b.songId);
            song = songs.find(song => song.songId > parseInt(songId));
            if (!song) {
                song = songs.find(song => song.songId !== parseInt(songId));
            }
        } else {
            song = songs.find(song => song.songId === parseInt(songId));
        } 

        setSongLoading(true);
        playerRef.current.contentWindow.postMessage({
            type: "switch-songs",
            body: {
                ... song,
                autoPlay: true
            }
        }, "*");
        setSongLoaded(song.songId);
        setLeakLoaded(undefined);
    }, [songId, playerRef]);

    const onClick = useCallback(() => {
        setWaitingForSlice(true);
        // tell the iframe "we want the last 30 sec of audio recorded"
        // the hook above (useRecordingListener), will wait for a return message
        // with the recording buffer
        playerRef.current.contentWindow.postMessage({
            type: "slice-audio",
            body: ""
        }, "*");
    }, [playerRef]);

    useEffect(() => {
        if (buffer && beatGuide && loopsData && endTime !== undefined) {
            setWaitingForSlice(false);
        }
    }, [setWaitingForSlice, buffer, beatGuide, loopsData, endTime]);

    return [
        <div className="slice-recorder">
          <img src="/HIFI-SAVE.svg"
               onClick={onClick}
               className={(waitingForSlice ? "pointer-events-none opacity-25 " : "") + "pushable slice-recorder"}>
          </img>
          <div className="rec-dot"></div>
        </div>
        ,
        (buffer && beatGuide && loopsData && endTime !== undefined) ? <SliceWaveform
                     zorbId={zorbId}
                     screenshot={screenshot}
                     remixesHistory={remixesHistory}
                     songId={songId}
                     silence={silence}
                     startStep={startStep}
                     beatGuide={beatGuide}
                     loopsData={loopsData}
                     endTime={endTime}
                     zorbMovements={zorbMovements}
                     stemsData={stemsData}
                     onCancel={(switchSongs=false) => {
                         onCancel();
                         setIsSlicing(false);
                         loadSong(switchSongs);
                         // load song
                     }}
                     buffer={buffer} waveform={waveform}/> : ''
    ];
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSongLoaded: x => dispatch(setSongLoaded(x)),
      setSongLoading: x => dispatch(setSongLoading(x)),
      setLeakLoaded: x => dispatch(setLeakLoaded(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceRecorder);
