import React from  'react';
import {SHADOW_FILTER} from 'components/ux/filters.js';
import CurveDiv from 'components/ux/CurveDiv.js';
import {walk, curve, relative, cut, radius} from 'components/ux/dsl.js';

export const AlgorithmicCurve = ({fill, relative, params, className, children, svgFilter}) => {
    return (
        <CurveDiv
          svgFilter={svgFilter || SHADOW_FILTER}
          fill={fill || "#ffffff29"}
          stroke="#ffffff26"
          relative={relative || false} algo={curveAlgorithm(params)} radius={params.radius} className={className}>
          {children}
        </CurveDiv>);
 
};

export const curveAlgorithm = (params) => {
    return [
        [
            walk(params.walk1),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(params.depth1a),
            walk(params.walk2),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(-params.depth1b)
        ],
        [],
        [
            walk(params.walk3),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(params.depth2a),
            walk(params.walk4),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(-params.depth2b)
        ],
        [
        ]
    ];
};
