import React, { useRef, useEffect, useCallback, useState} from 'react';
import {getLeakTime} from '../header/getLeakTime.js';
import Tone from 'tone';
import {AlgorithmicCurve} from './curve-algorithms.js';
import StemTraits  from './StemTraits.js';
import ENSName from 'components/ens/ENSName.js';
import {getZorb} from 'utils/getZorb.js';
import {SHADOW_FILTER} from 'components/ux/filters.js';
import CurveDiv from 'components/ux/CurveDiv.js';
import {walk, curve, relative, cut, radius} from 'components/ux/dsl.js';

export default ({params, stats={}, refs, setRefs, remix, playing, audioPlayer, setPlaying, isPlaying, togglePlay, play}) => {
    const ref = useRef();
    let [hover, setHover] = useState(false);
    const [zorb, setZorb] = useState();
    const [data, setData] = useState();
    useEffect(() => {
        let metadata = remix.metadata;
        setData(metadata);
        let zorbId = metadata.attributes.find(x => x.trait_type === "zorb").value;
        getZorb(zorbId).then(setZorb);
    }, [remix]);

    useEffect(() => {
        if (zorb && !refs.includes(ref)) {
            setRefs([... refs, ref]);
        }
    }, [zorb, refs, setRefs]);

    if (!data) {
        return '';
    }

    let traits = data.attributes;

    const DROP_ALGO = [
        [
            walk(params.walk1),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(params.depth1a),
            walk(params.walk2),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(-params.depth1b)
        ],
        [],
        [
            walk(params.walk3),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(params.depth2a),
            walk(params.walk4),
            curve()
                .start(params.a)
                .end(params.b)
                .tension(params.c)
                .depth(-params.depth2b)
        ],
        [
        ]
    ];


    return (
        <AlgorithmicCurve params={params} className={playing === remix ? "zorb-remix-option playing" : "zorb-remix-option"}>
        <div
          className="z-30 flex flex-col w-full cursor-pointer inner-card"
          onClick={async () => {
              await Tone.Master.context._context.resume();
              setPlaying(remix);
          }}
              ref={ref}>
              <div className="inner">
                <StemTraits traits={traits}/>
                {zorb && <img onMouseOver={() => setHover(true)} onMouseLeave={() => setHover(false)} className="zorb-image" src={zorb}/>}
                <div className="token-id">{remix.tokenId}</div>
                {/*<div className="caption">
                <img src={remix.metadata.image}/>
                </div>*/}
              </div>
              <div className={hover ? "hover-name show" : "hover-name"}><ENSName address={remix.owner}/></div>
          <div className="pt-1 leak-time flex text-xs text-center mx-auto color-zinc-200">
            <span className="mx-auto">
              {getLeakTime(remix)} ago
            </span>
          </div>
          {stats.deepRemixes > 0 && <div className="pt-1 remixes-count mt-auto flex mx-auto rounded-full px-3 text-xs">
                                          {stats.deepRemixes} <span className="mt-0.5 ml-1">𓍊</span></div>}
              {playing && <div
                            onClick={(e) => {
                                e.stopPropagation();
                                togglePlay();
                                // audioPlayer.stopSound()
                                setPlaying(undefined);
                            }}

                            className="pause-button"/>}
            </div>
        </AlgorithmicCurve>
    );
};

