import React, { useEffect, useCallback, useState} from 'react';
import ScrubUX from './ScrubUX.js';
import SongSelector from './SongSelector.js';
import TextAndTime from './TextAndTime.js';
import MultiplayerIcon from './MultiplayerIcon.js';
import {isMobile} from 'react-device-detect';
import CloseIcon from 'components/svg/CloseIcon.js';
import {useTokenOwners} from 'utils/zorbs.js';
import ENSName from 'components/ens/ENSName.js';
import Clock from './Clock.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {connect} from 'react-redux';
import CurveDiv from 'components/ux/CurveDiv.js';
import './header.scss';
import SimpleControls from 'components/player/SimpleControls.js';
import {walk, curve, relative, cut, radius} from 'components/ux/dsl.js';

const ZporeControlHeader = ({
    hideLeaks,
    nextLeak,
    chooseLeak,
    artist,
    songId,
    title,
    playerRef,
    setShowRemixes,
    setRemixes,
    remixes,
    tooltip,
    userZorbs,
    ... props
}) => { 

    let [isScrubbing, setIsScrubbing] = useState(false);
    let [showingSongs, setShowingSongs] = useState(false);

    useEffect(() => {
        getZporeRemixes(songId)
            .then(result => {
                setRemixes(result);
            });
    }, [songId]);

    let {zorbs} = useTokenOwners({account: {address: nextLeak && nextLeak.mintInfo.toAddress}});

    const DROP_ALGO = [
        [],
        [
            walk(140),
            curve()
                .start(10)
                .end(30)
                .tension(10)
                .depth(40),
            walk(30),
            curve()
                .start(10)
                .end(50)
                .tension(30)
                .depth(-20)
        ],
        [
        ],
        [
        ]
    ];

    let walkAmount = 160; //nextLeak ? 200 + 2*(nextLeak.song.title + nextLeak.song.artist).length : 10;
    const DROP_ALGO_2 = [
        [],
        [],
        [],
        [
            relative(true),
            walk(isMobile ? 38 : 32),
            curve()
                .start(window.A)
                .end(window.B)
                .tension(window.C)
                .depth(70),
            walk(isMobile ? 40 : 50),
            curve()
                .start(10)
                .end(12)
                .tension(30)
                .depth(-70)
            ]
    ];

    let leakCreator = nextLeak && nextLeak.mintInfo.toAddress;

    let zorb = zorbs && zorbs[0] && zorbs[0].image.url;
    let userZorb = userZorbs && userZorbs[0] && userZorbs[0].image.url;

    let dateLeaked = nextLeak && new Date(nextLeak.mintInfo.mintContext.blockTimestamp);

    let timeElapsed = dateLeaked && new Date().getTime() - dateLeaked.getTime();

    return [
        (isScrubbing && <div className="scrub-backdrop"/>),
        (<CurveDiv
           radius={24}
           className={"zpore-control-header next-leak-controller " + (nextLeak && !tooltip ? " showing" : " invisible")}
           algo={DROP_ALGO_2}
          svgFilter={{def: (
              <defs><filter id='shadow' color-interpolation-filters="sRGB">
                      <feDropShadow dx="5" dy="3" stdDeviation="3" flood-opacity="0.5"/>),
                      
                    </filter>
              </defs>),
                     name: "url(#shadow)"
                    }}
           fill="#ffffff33"
           stroke="transparent">
           <div className="flex z-30 flex-col  overflow-hidden inner-control">
             <div className="flex items-start">
               <div className="flex-col flex">
                 <div className="w-full pl-1 pt-4 pb-0 flex text-xs top-header">
                   <CloseIcon
                     className="close-btn z-30 w-4 h-5 pointer-events-auto" viewPort="0 0 12 12" version="1.1"
                     onClick={() => hideLeaks(15000)}
                   />
                   {zorb && <img className="zorb-icon-1 ml-2 w-4 h-4" src={zorb}/>} <ENSName className="mx-2" address={leakCreator}/> <TextAndTime timeElapsed={timeElapsed}/>
                 </div>
                 {/*<div className="pl-3 pb-3 flex">
                   <span className="text-xs">
                     <span className="italic ml-2">leaked</span> {leakTime(timeElapsed)} ago
                   </span>
                 </div>*/}
             </div>
               <div
                 onClick={() => chooseLeak()}
                 className="cursor-pointer flex items-start text-s ml-auto mr-2 my-auto px-3 py-1 load-into-spore bg-black text-white">
                 <div className="w-24">play multiplayer</div> <MultiplayerIcon zorb1={zorb} zorb2={userZorb}/>
               </div>
             </div>
           </div>
         </CurveDiv>),
        (<div
          svgFilter={{def: (
              <defs><filter id='shadow' color-interpolation-filters="sRGB">
                      <feDropShadow dx="5" dy="3" stdDeviation="3" flood-opacity="0.5"/>),
                      
                    </filter>
              </defs>),
                     name: "url(#shadow)"
                    }}
           relative={false}
           radius={12} className={(showingSongs ? "showing-songs " : "") + "zpore-control-header " + (nextLeak ? " " : "")} algo={DROP_ALGO} fill={showingSongs ? "#0000004a" : "#ffffff33"} stroke="transparent">
           <div className="flex z-20 flex-col  overflow-hidden inner-control">
             <div
                 onClick={() => setShowingSongs(!showingSongs)}
               className="w-full px-3 py-2 flex text-sm top-header">
               <div className="text-white">{title}</div>
               <div
                 className="ml-2 text-zinc-100 underline decoration-solid cursor-pointer">{artist}</div>
             </div>
             {showingSongs && <SongSelector hide={() => setShowingSongs(false)}/>}
             <div className={(showingSongs ? "invisible " : "") + "w-full pt-3 px-3 pb-1 flex"}>
                <div className="flex">
                  {playerRef && <SimpleControls playerRef={playerRef}/>}
                  <Clock className="text-white text-xs"/>
                </div>
                <div onClick={() => setShowRemixes(true)} className="ml-auto remixes-btn text-xs  text-white">
                  {remixes.length} remixes ↗
                </div>
              </div>
             <ScrubUX setIsScrubbing={setIsScrubbing}/>
           </div>
         </div>)
    ];
};

const leakTime  = (time) => {
    let minutes = time / 1000 / 60;
    if (minutes < 60) {
        return Math.ceil(minutes) + " min. ";
    }

    let hours = minutes / 60;
    if (hours < 48) {
        return Math.ceil(hours) + " hours "
    }

    let days = hours / 24;
    return Math.round(days) + " days ";
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef,
      a: window.A || 6,
      b: window.B || 60,
      c: window.C || 20
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZporeControlHeader);
