import React, {useRef, useState, useEffect, useCallback} from 'react';
import {shuffle} from 'utils/shuffle.js';
import {multiplayer} from './multiplayer.js';
import {fetchSongMetadata} from 'stems/getStems.js';
import {getDropAddress} from 'zpore/getDropAddress.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {getAttributeMapping} from 'erc721/attributes.js';


const MIN_LEAK = {
    0: 3,
    1: 3,
    2: 0,
    3: 5,
    4: 11
};

let counter=0;
const fetchAllLeaks = async (songId) => {
    console.log('fetch all leaks called with=', songId);
    counter++;
    let id = counter;
    let drops  = await getDropAddress();
    let songs = await fetchSongMetadata(undefined, true);
    let currentSong = songs.filter(x => x.songId === parseInt(songId));
    let leaks = [];
    for (let {songId, dropAddress} of drops) {
        let song = songs.find(x => x.songId === songId);
        let minTokenId = MIN_LEAK[songId] || 0;
        let remixes = await getZporeRemixes(songId, dropAddress);
        remixes = remixes.filter(
            x => parseInt(x.tokenId) >= minTokenId);
        remixes
            .forEach(x => {
                x.song = song;
                x.song.dropAddress = dropAddress;
                x.attributes = getAttributeMapping(x.metadata.attributes);
        });
        leaks = [... leaks, ... remixes];
    }
    // shuffle(leaks);
    let ret = sortAlgorithm(leaks, songId, currentSong);
    if (counter !== id) {
        return undefined;
    }
    return ret.filter(x => x);
};

const sortAlgorithm = (leaks, songId, currentSong) => {
    leaks = [... leaks];

    let dates = leaks.map(x => new Date(x.mintInfo.mintContext.blockTimestamp).getTime());
    dates.sort();
    dates = dates.reverse();
    let minDate = Math.min(... dates);

    leaks.forEach(
        leak => {
            leak.dateOrdering = dates.indexOf(new Date(leak.mintInfo.mintContext.blockTimestamp).getTime());
        });


    leaks.sort((a,b) => a.dateOrdering - b.dateOrdering);
    songId = parseInt(songId);

    return pickLeaks(
        leaks,
        {songId, range: 3, songProbability: 100},
        {songId, range: 6, songProbability: 100},
        {songId, songProbability: 80, range: 8},
        {songId, songProbability: 25, range: 10},
        {songId, songProbability: 25, range: 12},
        {songId, songProbability: 25, range: 14},
        {songId, songProbability: 60, range: 16},
        {songId, songProbability: 25, range: 18},
        {songId, songProbability: 60, range: 18},
        {songId, songProbability: 25, range: 18},
        {songId, songProbability: 60, range: 18},
        {songId, songProbability: 25, range: 18},
        {songId, songProbability: 60, range: 18},
        {songId, songProbability: 25, range: 18},
        {songId, songProbability: 25, range: 18},
    );
};

const pickLeaks = (list, ...items) => {
    let picked = [];
    let remaining = list;
    let i=0;
    for (let {songId, songProbability, range} of items) {
        if (remaining.length === 0) {
            break;
        }
        let remaining1 = remaining.filter(x => x.song.songId === songId);
        let remaining2 = remaining.filter(x => x.song.songId !== songId);
        let pick;
        if (Math.random()*100 <= songProbability && remaining1.length > 0) {
            console.log('chose song for i=%s', i);
            pick = remaining1[Math.ceil(range*Math.random()) % remaining1.length];
        } else { 
            console.log('chose not song for i=%s', i);
            pick = remaining2[Math.ceil(range*Math.random()) % remaining2.length];
        }
        picked.push(pick);
        remaining = remaining.filter(x => x !== pick);
        i++;
    }
    return [... picked, ... shuffle(remaining)];
};

