export const useLeaks = ({playStatus, songId}) => {

    let [leakTime, setLeakTime] = useState(5000);
    let [leaks, setLeaks] = useState([]);
    let [nextLeak, setNextLeak] = useState();
    let [loadedLeak, setLoadedLeak] = useState();

    let leakCounter = useRef(0);
    let interval = useRef();

    // when available -> true, and timer gets to 0, we show
    // the user a new leak
    let available = useRef(true);

    useEffect(() => {
        if (songId === undefined) {
            return;
        }
        fetchAllLeaks(songId).then( (x) => {
            if (!x) {
                return;
            }
            setNextLeak(undefined);
            available.current = true;
            setLeaks(x);
            leakCounter.current = 0;
            setLeakTime(5000);
        });

    }, [songId, setLeaks, setNextLeak]);

    useEffect(() => {
        clearInterval(interval.current);
        if (playStatus) {
            interval.current = setInterval(() => {
                if (available.current) {
                    let _nextLeak = leaks[leakCounter.current % leaks.length];
                    if (!_nextLeak || !_nextLeak.metadata) {
                        available.current = true;
                        leakCounter.current = leakCounter.current+1;
                        return;
                    }
                    let url = _nextLeak.metadata.animation_url;
                    _nextLeak.downloadedAudio = new Promise(async (resolve) => {
                        let resp = await fetch(url, {method: "GET"});
                        let json = await resp.arrayBuffer();
                        let blob = new Uint8Array(json);
                        resolve(blob);
                    });
                    setNextLeak(_nextLeak);
                    available.current = false;
                    leakCounter.current = leakCounter.current+1;
                }
            }, leakTime);
        } else {
        }

        return () => clearInterval(interval.current);
    }, [playStatus, leaks, setNextLeak, leakTime]);

    const chooseLeak = useCallback(() => {
        let leak = nextLeak;
        available.current = true;
        setLoadedLeak(leak);
        setNextLeak(undefined);
        multiplayer(songId, leak);
    }, [setNextLeak, setLoadedLeak, songId, nextLeak]);

    const hideLeaks = useCallback((time=5000) => {
        setNextLeak(undefined);
        setLeakTime(time);
        setLoadedLeak(undefined);
        available.current = true;
        leakCounter.current = leakCounter.current+1;
    }, [setLeakTime, setNextLeak, setLoadedLeak]);

    return {leaks, chooseLeak, nextLeak, loadedLeak, hideLeaks};
};

