import React, { useEffect, useCallback, useState} from 'react';
import RemixAnimation from './RemixAnimation.js';
import {connect} from 'react-redux';
import Colors from 'components/Colors.js';
import './remix-dna.scss';
const stc = require('string-to-color');

const colors = new Colors();


const color = (t) => {
    return colors.saturate(stc(t), 4, 1, 50);
};

const SIZE = 50;

const RemixDNADetails = ({remix, remixDNA, attributes, animationData, duration, isPlaying}) => { 
    let stemKeys = Object.keys(attributes).filter(x => x.includes("Stem"));

    let stems = {};
    for (let key of stemKeys) {
        let [stem, contractAddress, tokenId] = key.split(" ");
        if (!stems[contractAddress]) {
            stems[contractAddress] = [];
        }
        let value = attributes[key];
        stems[contractAddress].push({
            contractAddress,
            tokenId: parseInt(tokenId.slice(1)),
            percentage: value
        });
    }
    console.log("STEMS parsed=", stems);
    return (
        <div className="remix-dna-details relative">
        {Object.keys(stems).map(
            contractAddress =>
            <div className="contract-container">
            {stems[contractAddress].map(
                remixAtom =>
                <div
                style={{
                    width: SIZE * remixAtom.percentage / 100,
                    height: SIZE * remixAtom.percentage / 100,
                    borderRadius: SIZE * remixAtom.percentage / 100,
                    backgroundColor: color(contractAddress + ' ' + remixAtom.tokenId)
                }}
                  className="remix-atom">
                </div>)}
            </div>
        )}
          {animationData && <RemixAnimation animationData={animationData} isPlaying={isPlaying} duration={duration}/>}
          <div className="remix-bpm text-xs rounded-full px-3 flex absolute bottom-2 right-2">
            <span className="mr-2 ">BPM</span>
            <span className="text-zinc-100">{Math.round(attributes["bpm"])}</span>
          </div>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixDNADetails);
