import React, { useEffect, useCallback, useState} from 'react';
import {SHADOW_NOISE_FILTER} from 'components/ux/filters.js';
import {getAttributeMapping} from 'erc721/attributes.js';
import RemixDNADetails from './RemixDNADetails.js';
import {AlgorithmicCurve} from './curve-algorithms.js';
import DatGui, { DatBoolean, DatColor, DatNumber, DatString } from 'react-dat-gui';
import {connect} from 'react-redux';

const RemixDetails = ({remix, isPlaying, duration}) => { 
     const [params, setParams] = useState({
        a: 10,
        b: 17,
        c: 12,
        walk1: 24,
        walk2: 41,
        walk3: 65,
        walk4: 0,
        depth1a: 10,
        depth1b: 10,
        depth2a: 10,
        depth2b: 12,
        radius: 52,
    });

    const [animationData, setAnimationData] = useState(undefined);

    useEffect(() => {
        console.log("remix metadata = ",  remix.metadata);
        fetch(remix.metadata.image_url).then(
            async x => {
                try {
                    let json = await x.json();
                    setAnimationData(json);
                } catch(e) {
                    setAnimationData(undefined);
                    console.log('failed to parse');
                }
            });
    }, [remix, setAnimationData]);

    const guiUpdate = (newData) => setParams({ ...params, ...newData });
        /*<DatGui data={params} onUpdate={guiUpdate} liveUpdate={true}>
            <DatNumber path="a" label="a" min={0} max={100} step={1} />
            <DatNumber path="b" label="b" min={0} max={100} step={1} />
            <DatNumber path="c" label="c" min={0} max={100} step={1} />
            <DatNumber path="walk1" label="walk1" min={0} max={100} step={1} />
            <DatNumber path="walk2" label="walk2" min={0} max={100} step={1} />
            <DatNumber path="walk3" label="walk3" min={0} max={100} step={1} />
            <DatNumber path="walk4" label="walk4" min={0} max={100} step={1} />
            <DatNumber path="depth1a" label="depth1a" min={0} max={100} step={1} />
            <DatNumber path="depth1b" label="depth1b" min={0} max={100} step={1} />
            <DatNumber path="depth2a" label="depth2a" min={0} max={100} step={1} />
            <DatNumber path="depth2b" label="depth2b" min={0} max={100} step={1} />
            <DatNumber path="radius" label="radius" min={0} max={100} step={1} />

            </DatGui>*/
 
    return (
         
        <AlgorithmicCurve svgFilter={SHADOW_NOISE_FILTER} fill="#323131bf" relative={true} params={params} className="remix-details">
          <div className="inner-details p-5 flex-1 flex flex-col z-30">
            <div className="name-text mb-3 text-lg">{formatName(remix.metadata.name)}</div>
            <div className="token-text mb-3 text-lg">{formatToken(remix.tokenId)}</div>
            <img className="logo-small" src="/Subtract.svg"/>
            <RemixDNADetails
              duration={duration}
              isPlaying={isPlaying}
              animationData={animationData}
              remix={remix}
              attributes={getAttributeMapping(remix.attributes)}
              remixDNA={remix.metadata.remixDNA}/>
          </div>
        </AlgorithmicCurve>);
};

const formatName = (x) => {
    return x.slice(0, x.indexOf("("));
};


const formatToken = (x) => {
    x = x.toString()
    while (x.length < 4) {
        x = '0' + x;
    }
    return x;
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixDetails);
