import React, { useEffect, useCallback, useState} from 'react';
import {useInterval} from 'components/hooks/useInterval.js';
import {connect} from 'react-redux';

const ScrubBar = ({audioPlayer, startTime, isPlaying} ) => { 
    let [percentage, setPercentage] = useState(0);

    const onTick = useCallback(() => {
        if (isPlaying) {
            setPercentage(((new Date().getTime() - startTime.current)/1000)/(audioPlayer.buffer.duration)*100 % 100);
        }
    }, [isPlaying, audioPlayer]);
    useInterval(onTick, 20);
    return (
        <div className="scrub-bar bg-black h-4 w-full">
          <div
            style={{left: .9*percentage + "%"}}
            className="playhead w-8 h-8 bg-white">
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ScrubBar);
