import React, { useEffect, useCallback, useState} from 'react';
import LoadingRemix from 'components/zpore/remixes/LoadingRemix.js';
import {connect} from 'react-redux';
import {setSongLoaded, setLeakLoaded} from 'actions/index.js';

import './song-selector.scss';
import {getSongs} from 'stems/getStemz.js';
const SongSelector = (props) => { 
    let [songs, setSongs] = useState([]);
    let [loading, setLoading] = useState();
    useEffect(() => {
        getSongs().then((s) => setSongs(s.filter(x => !x.title.includes("Psilocybin"))));
    }, []);

    const loadSong = useCallback((song) => {
        setLoading(song.songId);
        props.playerRef.current.contentWindow.postMessage({
            type: "switch-songs",
            body: {
                ... song,
                autoPlay: true
            }
        }, "*");
        console.log('setting song loaded=', song.songId, song);
        props.setSongLoaded(song.songId);
        props.setLeakLoaded(undefined);
    }, [props.playerRef, setLoading]);

    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    }, []);
    const onMessage = useCallback((e) => {
        if (e.data.type === "song-switch-completed") {
            props.hide();
        }
    }, [setLoading]);

    return (<div onClick={e => e.stopPropagation()} className="pt-4 pb-5 song-selector">
              <div className="header-columns">
                <div className="p-2 flex text-xs">
                  <div className="w-48 text-center">title</div>
                  <div className="w-48 text-center">artist</div>
                  <div className="w-16 text-center">tempo</div>
                </div>
              </div>
            {songs.map(
                song =>
                <div
                  onClick={() => loadSong(song)}
                  className={loading === song.songId === song.songId || (parseInt(props.songLoaded) === song.songId ? "bg-zinc-500 " : "") + "pl-2 py-1 flex cursor-pointer text-xs rounded-lg song-option"}>
                  <div className="w-48">{song.title}</div>
                  <div className="w-48 text-zinc-300">{song.artist}</div>
                  <div className="w-16 text-zinc-300">{song.bpm}</div>
                  {loading == song.songId && <LoadingRemix radius={16}/>}
                </div>)}
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
      songLoaded: state.spore.songLoaded,
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSongLoaded: x => dispatch(setSongLoaded(x)),
      setLeakLoaded: x => dispatch(setLeakLoaded(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SongSelector);
