import React, { useEffect, useRef, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './scrub.scss';

const ScrubUX = ({playerRef, setIsScrubbing, isScrubbing}) => { 
    let mouseDown = useRef(false);
    let ref = useRef();

    let [mouseProgress, setMouseProgress] = useState(0);
    let [clock, setClock] = useState({
        duration: 0,
        progress: 0,
    });

    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    }, [setClock, clock]);

    const onMessage = useCallback((e) => {
        if (e.data.type === "song-position") {
            setClock({
                ... clock,
                progress: e.data.body
            });
        }
        if (e.data.type === "song-duration") {
            setClock({
                ... clock,
                duration: e.data.body
            });
        }
    }, [setClock, clock]);
 
    const onMouseDown = useCallback(() => {
        mouseDown.current = true;
        setIsScrubbing(true);
    }, [setIsScrubbing]);
    const release = useCallback(() => {
        mouseDown.current = false;
        setIsScrubbing(false);
    }, [setIsScrubbing]);

    useEffect(() => {
        window.addEventListener("mouseup", release);
        return () => window.removeEventListener("mouseup", release);
    }, [setIsScrubbing]);

    useEffect(() => {
        window.addEventListener("mousemove", onMouseMove);
        window.addEventListener("touchmove", onMouseMove);
        return () => {
            window.removeEventListener("mousemove", onMouseMove);
            window.removeEventListener("touchmove", onMouseMove);
        }
    }, [playerRef, setMouseProgress]);

    const onMouseMove = useCallback((e) => {
        if (!playerRef || !playerRef.current || !mouseDown.current) {
            return;
        }
        if (e.touches) {
            e = e.touches[0];
        }
        let left = e.clientX - ref.current.getBoundingClientRect().left
        let width = ref.current.offsetWidth;
        let percentage = 100*left / width;
        setMouseProgress(Math.max(0, Math.min(100,percentage)));
        playerRef.current.contentWindow.postMessage({
            type: "seek-to-percentage",
            body: Math.max(0, Math.min(100,percentage))
        }, "*");
    }, [playerRef, setMouseProgress]);


    let _progress = mouseDown.current ? mouseProgress : clock.progress;
    let color = `hsl(${(180 + _progress/100*360.0) % 360.0}deg 83% 55%)`;
    return (
        <div
          ref={ref}
          onMouseDown={onMouseDown}
          onTouchStart={onMouseDown}
          className="scrub-bar h-3 w-5/6 bg-black">
          <div style={{width: _progress + '%'}} className="absolute w-8 h-4 inner-playbar"/>
          <div style={{
              backgroundColor: color,
              left: _progress + '%'}} className="absolute w-8 h-6 playhead rounded-full"/>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ScrubUX);
