import React, { Component, useCallback } from 'react';
import Tone from 'tone';
import './buttons.scss';

const PlayButton = (props) => {
    const onPlayClicked = useCallback((e) => {
        e.stopPropagation();
        e.target.parentNode.blur();
        e.target.blur();
        e.currentTarget.blur();

        if (props.resume) {
            if (Tone.Master.context._context.state === "suspended") {
                Tone.Master.context._context.resume().then(
                    x => 
                    props.play());
            } else {
                props.play();
            }
        } else {
            props.play();
        }
    }, [props.isPlaying]);

    return (
        <div
          style={props.style || {}}
          onClick={onPlayClicked}
          onMouseDown={(e) => e.stopPropagation()}
          onTouchStart={(e) => e.stopPropagation()}
          className="play-btn-wrapper">
          <div
            style={{
                borderColor: props.isPlaying ? props.color: "transparent transparent transparent " + props.color,
            }}
            className={props.isPlaying ? "play-btn playing" : "play-btn "}/>
        </div>
    );
}


export default PlayButton;
