import React, { useEffect, useRef, useCallback, useState} from 'react';
import {getDropAddress} from 'zpore/getDropAddress.js';
import CurrentlyPlaying from './CurrentlyPlaying.js';
import {PLAYING} from 'constants/play-statuses.js';
import {togglePlayback} from 'player/controls.js';
import {useAudioPlayer} from 'components/hooks/useAudioPlayer.js';
import ipfs from 'decentralized/Ipfs.js';
import AudioUnit from 'engine/audio.js';
import {REMIX_COLLECTION_ADDRESS} from 'constants/zpore-addresses.js';
import {getZporeRemixes} from 'zpore/getZporeRemixes.js';
import {connect} from 'react-redux';
import ZorbRemixView from './ZorbRemixView.js';
import './remixes.scss';

const ZorbRemixes = ({remixes, remixId, songId, show, setRemixId, setShow, playerRef, playStatus}) => { 
    let [soundURL, setSoundURL] = useState();
    let [autoPlay, setAutoPlay] = useState(true);
    let {audioPlayer, stop, loading, togglePlay, startTime, isPlaying, play} = useAudioPlayer(soundURL, autoPlay, true);
    let [playing, setPlaying] = useState();
    let [remixStats, setRemixStats] = useState({});
    let alreadyLoaded = useRef(false);

    useEffect(() => {
        if (playerRef && playerRef.current && playerRef.current.contentWindow) {
            playerRef.current.contentWindow.postMessage({
                type: "sleep-ui",
                body: show ? 1 : 0
            }, "*");
        }
    }, [show, playerRef]);

    useEffect(() => {
        if (playing) {
            setRemixId(playing.tokenId);
        }
    }, [playing, setRemixId]);

    useEffect(() => {
        if (!songId) {
            return;
        }
        console.log('songId=', songId);
        getDropAddress(songId).then(
            async dropAddress => {
                let url = 'https://zequencer.io/searchRemixes/' + dropAddress;
                let res = await fetch(url);
                let json = await res.json();
                console.log("STATS=",json);
                setRemixStats(json);
            });
    }, [songId]);

    useEffect(() => {
        if (!show && playing) {
            stop();
            setPlaying(undefined);
        }
    }, [setPlaying, playing, show, stop]);

    useEffect(() => {
        if (remixId === undefined) {
            alreadyLoaded.current = true;
        }
    }, []);

    useEffect(() => {
        if (remixId !== undefined && !alreadyLoaded.current) {
            setAutoPlay(false);
            let remix = remixes.find(x => x.tokenId === remixId);
            if (remix) {
                setPlaying(remix);
                alreadyLoaded.current = true;
            }

        }
    }, [setAutoPlay, remixId, setPlaying, remixes]);


    useEffect(() => {
        if (playing) {
            let sound = playing.metadata.animation_url;
            setSoundURL(sound);
        } else {
            setSoundURL(undefined);
        }
    }, [playing, audioPlayer, remixes, setSoundURL]);

    useEffect(() => {
        if (show && playStatus === PLAYING) {
            togglePlayback(playerRef.current);
        }
    }, [show, playStatus, playerRef]);

    if (show) {
        return [<ZorbRemixView
                  remixStats={remixStats}
                  songId={songId}
                  audioPlayer={audioPlayer}
                  setAutoPlay={setAutoPlay}
                  isPlaying={isPlaying}
                  togglePlay={togglePlay}
                  play={play}
                  playing={playing}
                  setPlaying={(x) => {
                      setAutoPlay(true);
                      setPlaying(x);
                  }}
                 hide={() => {
                     if (audioPlayer) {
                         audioPlayer.stopSound();
                     }
                     setShow(false);
                     setRemixId(undefined);
                 }} remixes={remixes}/>,
                <CurrentlyPlaying
                  songId={songId}
                  remixId={remixId}
                  duration={audioPlayer && audioPlayer.duration}
                  remixStats={remixStats}
                  startTime={startTime}
                  loading={loading}
                  isPlaying={isPlaying}
                  togglePlay={togglePlay}
                  playing={playing}
                  audioPlayer={audioPlayer}/>];
    }
    return '';
    /*
    return (
        <div
          onClick={() => setShow(true)}
          className="remixes-button">
          <span>{remixes.length} Remixes 𓍊</span>
        </div>);
        */
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef,
      playStatus: state.playback.playStatus
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbRemixes);
